import React, { Component } from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';

class Portfolio extends Component {
  constructor(props) {
    super(props);

    this.state = {
      lightboxIsOpen: false,
      currentImage: 0
    };
  }

  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1
    });
  }

  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1
    });
  }

  closeLightbox() {
    this.setState({
      lightboxIsOpen: false
    });
  }

  openLightbox(currentImage) {
    this.setState({
      lightboxIsOpen: true,
      currentImage
    });
  }

  clickThumbnail(currentImage) {
    this.setState({
      currentImage
    });
  }

  render() {
    const photos = [
      {
        caption: '店内',
        url: '/assets/images/shop/store.jpg'
      },
      {
        caption: '待合室',
        url: 'assets/images/shop/waiting.jpg'
      },
      {
        caption: '洗車',
        url: 'assets/images/shop/washing.jpg'
      },
      {
        caption: 'ポリッシュ',
        url: 'assets/images/shop/polishing.jpg'
      }
    ];

    const Items = ({ data }) => {
      return data.map((d, i) => (
        <div key={i}>
          <img alt={d.caption} src={d.url} />
          <p className='legend'>{d.caption}</p>
        </div>
      ));
    };

    return (
      <section className='bg-light text-black' id='portfolio'>
        <div className='container text-center'>
          <h3 className='text-gold'>SHOP</h3>
          <h2>店内の様子</h2>
          <div className='row no-gutters'>
            <Carousel>
              <Items data={photos} />
            </Carousel>
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;
